@import "../styles/variables.scss";
@import "../styles/mixins.scss";

html [data-theme="dark"] {
  .weather-form {
    background-color: $color-background-dark;
    box-shadow: $shadow-card-dark;
    &__input {
      .MuiInputBase-input {
        color: $color-text-dark;
      }

      .MuiInputLabel-root {
        color: $color-text-dark;
      }

      .MuiOutlinedInput-root {
        .MuiOutlinedInput-notchedOutline {
          border-color: $color-text-dark;
        }
        &:hover .MuiOutlinedInput-notchedOutline {
          border-color: $color-text-dark;
        }
      }

      background-color: darken($color-background-dark, 5%);
    }

    &__submit {
      background-color: $color-button-dark;
      color: $color-text-dark;
      &:hover {
        background-color: $color-button-hover-dark;
      }
    }
  }
}

html .weather-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  background-color: $color-background-form;
  border-radius: 8px;
  box-shadow: $shadow-form;
  max-width: 500px;
  width: 100%;
  margin: 20px auto;

  &__input {
    width: calc(100% - 40px);
    margin: 10px;
  }

  &__submit {
    height: 56px;
    box-sizing: border-box;
    margin-bottom: 10px;
    width: calc(100% - 40px);
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    background-color: #005a9c;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    &:hover {
      background-color: #004080;
    }
  }

  @media (min-width: 480px) {
    &__input,
    &__submit {
      height: 56px;
      width: 90%;
    }
  }

  @media (min-width: 768px) {
    flex-direction: row;
    align-items: baseline;

    justify-content: space-between;
    padding: 20px;

    &__input {
      flex: 1 0 auto;
      width: auto;
      margin-right: 10px;
      margin-bottom: 0;
    }

    &__submit {
      flex: 0 1 auto;
      padding: 10px 25px;
      width: auto;
    }
  }

  @media (min-width: 1024px) {
    &__input {
      margin-right: 20px;
    }
  }
}
